<template>
<div class="place">
  <div class="container">
    <h3 class="light text-center">
      
      <datepicker
        language="th"
        :disabled="disabled"
        :value="new Date(2017, 10, 26)"
        wrapper-class="d-inline-block"
        format="dd MMMM yyyy"
        input-class="datepicker-input"
      ></datepicker>
       <i> .. 1</i> ()
    </h3>
    <div style="margin-top: 60px">
      <h3 class="text-center"></h3>
      <ol style="font-size:1.2em;" class="medium">
        <li><u></u> .. 1 ()  <u></u> </li>
        <li><u></u>    </li>
        <li><u>Portfolio</u>     Portfolio </li>
      </ol>
    </div>
    <div style="margin-top: 60px" class="row align-items-center">
      <div class="col-lg-7">
        <h3 class="text-center"></h3>
        <ol style="font-size:1.2em;" class="medium">
          <li> BTS    2</li>
          <li> MRT    2 </li>
          <li>  15, 77, 155, 504, 177, 76</li>
        </ol>
      </div>
      <div class="col-lg-5">
        <gmap-map
          :center="center"
          :zoom="17"
          class="gmap"
        >
          <gmap-marker
            :key="index"
            v-for="(m, index) in markers"
            :position="m.position"
            :clickable="true"
            :draggable="true"
            @click="center=m.position"
          ></gmap-marker>
        </gmap-map>
        <div class="text-center">
          <i><small class="light"> .. 1 ()</small></i>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
  components: { Datepicker },
  data () {
    return {
      disabled: { days: [0, 1, 2, 3, 4, 5, 6] },
      center: { lat: 13.7275053, lng: 100.5326877 },
      markers: [{ position: { lat: 13.7275053, lng: 100.5326877 } }]
    }
  }
}
</script>

<style scoped>
.place {
  padding-top: 60px;
}

.gmap {
  width: 100%;
  height: 300px;
}

u {
  font-weight: bold;
}
</style>



